@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Noto+Sans+TC:wght@100;300;400;500;700;900&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Orbitron:wght@400;500;600;700;800;900&family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/* === Universal Setting ========================================== */
* {
  margin: 0;
  padding: 0;
  border: none;
}
body {
  display: flex;
  font-family: 'Prompt', 'Noto Sans TC';
  font-size: 16px;
  color: #333;
  background: #0f0f0f;
  height: 100%;
  width: 100%;
  overflow: hidden;
  /* scroll-behavior: smooth; */
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  user-select: none;
}

#root {
  width: 100%;
}

.target-fix {
  position: relative;
  top: -30px;
  display: block;
  height: 0;
  overflow: hidden;
}

#Tips {
  z-index: 5001;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: calc(100% - 4rem);
  max-width: 480px;
  min-width: calc(264px - 4rem);
  bottom: calc(60px + 1rem);
  padding: 0.7rem 0.7rem 0.7rem 0.9rem;
  background: #3636369a;
  margin-left: 2rem;
  box-shadow: 0 3px 20px 0 #000;
  border-radius: 0.8rem;
  -webkit-border-radius: 0.8rem;
  -moz-border-radius: 0.8rem;
  -ms-border-radius: 0.8rem;
  -o-border-radius: 0.8rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
/* 提示區塊 */
#Tips div span {
  color: #e1e1e1;
  font-size: 1rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
#closeBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 23px;
  width: 23px;
  background: #8a47479a;
  border-radius: 0.3rem;
  -webkit-border-radius: 0.3rem;
  -moz-border-radius: 0.3rem;
  -ms-border-radius: 0.3rem;
  -o-border-radius: 0.3rem;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.BtnLineL {
  position: absolute;
  height: 1px;
  width: 12px;
  border-radius: 0.5px;
  background: #bc8888;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.BtnLineR {
  position: absolute;
  height: 1px;
  width: 12px;
  border-radius: 0.5px;
  background: #bc8888;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
#closeBtn:hover {
  width: 25px;
  height: 25px;
  margin-right: -1px;
  background: #8a4747af;
  border-radius: calc(0.3rem + 2px);
  -webkit-border-radius: calc(0.3rem + 1px);
  -moz-border-radius: calc(0.3rem + 2px);
  -ms-border-radius: calc(0.3rem + 2px);
  -o-border-radius: calc(0.3rem + 2px);
}
#closeBtn:hover > .BtnLineL {
  width: 14px;
  background: #c99999;
}
#closeBtn:hover > .BtnLineR {
  width: 14px;
  background: #c99999;
}

#ctrlCourseScheduleInfo {
  z-index: 5003;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: calc(48px + 1rem);
  right: 1rem;
  height: 3rem;
  width: 3rem;
  background: #37383c;
  transform: rotate(1deg);
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  transition: 0.3s;
  color: #b2b2b2;
  font-size: 1.3rem;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  -webkit-transform: rotate(0.1deg);
  -moz-transform: rotate(1deg);
  -ms-transform: rotate(1deg);
  -o-transform: rotate(1deg);
}
#ctrlCourseScheduleInfo.open {
  transform: rotate(359.9deg);
  background: #137aa9;
  color: #fff;
  font-size: 1.9rem;
  -webkit-transform: rotate(359.9deg);
  -moz-transform: rotate(359.9deg);
  -ms-transform: rotate(359.9deg);
  -o-transform: rotate(359.9deg);
}

/* 安裝提示 */
#pwaInstallTips {
  z-index: 9999;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #1c1c1caf;
  overflow: scroll;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  scroll-snap-type: y mandatory;
}
.InstallTips {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  scroll-snap-align: start;
  transition: 2s;
  -webkit-transition: 2s;
  -moz-transition: 2s;
  -ms-transition: 2s;
  -o-transition: 2s;
}
.InstallTips > h1 {
  max-width: 400px;
  text-align: center;
  margin-bottom: 1rem;
  color: #ffffffcf;
}
.InstallTips > h1 > span {
  white-space: nowrap;
}
.btnBlock {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
.btnBlock > h5 {
  color: #09658fa8;
}
.btnBlock span {
  margin-top: 1rem;
  margin-bottom: 3rem;
  color: #ffffffaf;
  font-size: 1.5rem;
  font-weight: 500;
}
.startInstall {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
  width: 12rem;
  padding: 12px 18px;
  background: linear-gradient(160deg, #095e8fa8, #098f6ba8);
  border-radius: calc(0.75rem + 6px);
  -webkit-border-radius: calc(0.75rem + 6px);
  -moz-border-radius: calc(0.75rem + 6px);
  -ms-border-radius: calc(0.75rem + 6px);
  -o-border-radius: calc(0.75rem + 6px);
  font-weight: 700;
  color: #ffffffcf;
  box-shadow: 0 0 30px -5px #ffffff5f;
}
.refuseInstall {
  display: flex;
  justify-content: center;
  margin-top: 0.5rem;
  font-size: 1.5rem;
  width: 12rem;
  font-size: 1rem;
  padding: 12px 18px;
  border-radius: 23px;
  -webkit-border-radius: 23px;
  -moz-border-radius: 23px;
  -ms-border-radius: 23px;
  -o-border-radius: 23px;
  font-weight: 500;
  color: #7f7f7f;
}

#InstallationGuide {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  min-height: 100vh;
  width: 100%;
  padding: 100px 50px;
  scroll-snap-align: start;
}
.GuideTitle {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  max-width: 720px;
}
#InstallationGuide .GuideTitle h2 {
  color: #fff;
}
#InstallationGuide .GuideTitle span {
  color: #c1c1c1;
}

