}

/* 網頁更新提示 */
#update {
  z-index: 9000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: calc(100% - 4rem);
  max-width: 480px;
  min-width: calc(264px - 4rem);
  bottom: calc(60px + 1rem);
  padding: 0.7rem 0.7rem 0.7rem 0.9rem;
  background: #d846469a;
  margin-left: 2rem;
  box-shadow: 0 3px 20px 0 #000;
  border-radius: 1.15rem;
  -webkit-border-radius: 1.15rem;
  -moz-border-radius: 1.15rem;
  -ms-border-radius: 1.15rem;
  -o-border-radius: 1.15rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.updateContent p {
  color: #de8e8e;
  font-size: 1rem;
  font-weight: 500;
}
.updateBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 2rem;
  padding: 0 0.2rem;
  background: #e05c5c9a;
  border-radius: 0.525em;
  -webkit-border-radius: 0.525em;
  -moz-border-radius: 0.525em;
  -ms-border-radius: 0.525em;
  -o-border-radius: 0.525em;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
.updateBtn div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  color: #fdc0c0;
  font-size: 0.8rem;
  font-weight: 700;
}

/* === Universal Background Setting ========================================== */
@supports ((-webkit-backdrop-filter: initial) or (backdrop-filter: initial)) {
  #Tips,
  #update,
  #pwaInstallTips {
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
}

.bg-blue-green {
  background: linear-gradient(150deg, #3e71c2, #42b883);
  box-shadow: 0 0 40px 0 inset #9f9f9fa1;
}
.bg-gray-deepgray {
  background: linear-gradient(150deg, #5f5f5f, #353535);
}
.bg-black {
  background: #0f0f0f !important;
}
.bg-purple-blue {
  background: linear-gradient(150deg, #7a43be, #515ebf);
  box-shadow: 0 0 40px 0 inset #dfdfdfa1;
}
.bg-purple-blue2 {
  background: linear-gradient(150deg, #522d7f88, #3a438986);
  box-shadow: 0 0 40px 0 inset #292929af;
}
.bg-red-orange {
  background: linear-gradient(150deg, #c23e3e, #b87142);
  box-shadow: 0 0 40px 0 inset #9f9f9fa1;
}
.bg-red-orange2 {
  background: linear-gradient(150deg, #c23e3e8d, #b871428d);
  box-shadow: 0 0 40px 0 inset #292929af;
}
.bg-history {
  background: #000000;
  box-shadow: 0 0 40px 0 #1c1c1c;
}

/* === Universal Style Setting ========================================== */

main {
  position: absolute;
  width: 100%;
  min-width: 264px;
  height: calc(100vh - 108px);
  top: 48px;
  bottom: 48px;
  overflow: scroll;
}
.display-none {
  display: none;
}
.opacity-0 {
  opacity: 0;
}
.navSpace {
  height: 48px;
}
#refreshBtn {
  position: fixed;
  z-index: 9000;
  right: 12px;
  bottom: 72px;
  height: 48px;
  width: 48px;
  background: #df5050;
  color: #fff;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
}

/* === Nav ========================================== */
@media (min-width: 1px) {
  nav {
    z-index: 1000;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 48px;
    width: 100%;
    min-width: 264px;
    background-color: #1c1c1c;
  }
  #header {
    z-index: 1;
    display: flex;
    align-items: center;
    height: 48px;
    width: 100%;
    background: #1c1c1c;
    box-shadow: 0 5px 5px 0px #0f0f0f;
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  #header.open {
    box-shadow: 0 5px 5px 0px #1c1c1c;
  }
  #header-ul {
    display: flex;
    justify-content: space-between;
    height: 100%;
    width: 100%;
  }
  #header-li {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    height: 100%;
    width: 48px;
  }
  #header-li img.icon {
    height: 32px;
    width: 32px;
  }
  .classdata-icon a {
    display: flex;
    align-items: center;
  }
}

/* ======================= Menu ====================== */
@media (min-width: 1px) {
  menu {
    z-index: -1;
    position: fixed;
    height: 0;
    width: 100%;
    min-width: 264px;
    top: 48px;
    left: 0;
    opacity: 1;
    background: linear-gradient(180deg, #1c1c1c, #0f0f0f);
    pointer-events: none;
    transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    pointer-events: none;
  }
  menu.open {
    height: calc(100vh - 96px);
    opacity: 1;
    pointer-events: all;
    overflow: auto;
  }
  #menu-view {
    display: block;
    margin-top: -60vh;
    width: 90%;
    margin-left: 5%;
    transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  }
  #menu-view.show {
    margin-top: 0;
    opacity: 1;
  }
  #menu-block {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 24px 0;
    width: 100%;
  }
  #menu-block span {
    font-size: 18px;
    color: #7e7e7e;
  }
  #menu-block ul {
    padding: 12px 0;
  }
  #menu-linkView li h1 {
    font-size: 18px;
    font-weight: 700;
    color: #fff;
  }
  #menu-linkView {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    max-width: 720px;
  }
  .menu-link {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: calc(100% - 20px);
    margin: 10px;
    background-position: top;
    background-size: cover;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
  }
  .menu-link.close {
    opacity: 0;
  }
  .menu-link:hover {
    margin: 10px 5px 10px 5px;
    width: calc(100% - 10px);
  }
  .menu-link h1 {
    margin: 13px;
  }
  .menu-link img {
    position: relative;
    height: 46px;
    margin-left: 5px;
  }
  .filter {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.1);
  }

  .menuicon:hover > span {
    background-color: #e1e1e1;
    width: 19px;
    transition: 0.25s;
    -webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -ms-transition: 0.25s;
    -o-transition: 0.25s;
  }
  /* === 菜單按鈕 === */
  .menuicon-top {
    display: block;
    position: absolute;
    top: 20px;
    width: 17px;
    height: 1px;
    background: #ffffffaa;
    border-radius: 0.5px;
    -webkit-border-radius: 0.5px;
    -moz-border-radius: 0.5px;
    -ms-border-radius: 0.5px;
    -o-border-radius: 0.5px;
    transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .menuicon-top.active {
    top: 24px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transition: top 0.2s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: top 0.2s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: top 0.2s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: top 0.2s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: top 0.2s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .menuicon-bottom {
    display: block;
    position: absolute;
    bottom: 20px;
    width: 17px;
    height: 1px;
    background: #ffffffaa;
    border-radius: 0.5px;
    -webkit-border-radius: 0.5px;
    -moz-border-radius: 0.5px;
    -ms-border-radius: 0.5px;
    -o-border-radius: 0.5px;
    transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -webkit-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -moz-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -ms-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
    -o-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
      transform 0.2s cubic-bezier(0.5, 0, 0.3, 1);
  }
  .menuicon-bottom.active {
